<!--  -->
<template>
<div class='car'>
    <div class='edit'>
        <p class='edit-text edit-texts' @click='del_show=false' v-if='del_show' >完成</p>
        <p class='edit-text ' @click="del_show=true" v-else>编辑</p>
    </div>
    <div class='car-box'>
        <div class='car-list' v-for='(item,index) in carList'  :key='index'>
            <img src="../../../static/image/radios.png" alt="" class='choose-icon'  v-if='item.selected' @click='select(item.productId)'>
             <img src="../../../static/image/radio.png" alt="" class='choose-icon' v-else @click='select(item.productId)'>
            <img :src="item.img" alt="" class='car-img' >
            <div class='car-fr'>
                <div class='car-name'>{{item.title}}</div>
                <div class='fr-btm'>
                    <div class='car-money'>
                        <p class='money-old'>￥{{item.marketPrice}}</p>
                        <p class='money-now'>￥{{item.sellPrice}}</p>
                    </div>
                    <div class='car-num'>
                        <img src="../../../static/image/jian1.png" alt="" class='num-icon'  v-if='item.num==1'>
                         <img src="../../../static/image/jian.png" alt="" class='num-icon' @click='jianNum(item.productId)' v-else>
                        <span class='car-nums'>{{item.num}}</span>
                        <img src="../../../static/image/jia.png" alt=""  class='num-icon' @click='addNum(item.productId)' >
                    </div>
                </div>
                
            </div>
        </div>
    </div>
    <div class='car-btm'>
        <div class='btm-fl' @click="selectAll()">
            <img src="../../../static/image/radios.png" alt="" class='choose-icon' v-if='allSelsect' >
            <img src="../../../static/image/radio.png" alt="" class='choose-icon' v-else >
            <span class='all-text'>全选</span>
        </div>
        <div class='all-money'>共计：￥{{allMoney}}</div>
        <div class='del' v-if='del_show' @click="del()">删除</div>
        <div class='pay' v-else>结算</div>
    </div>
</div>
</template>

<script>
    import { mapActions ,mapGetters} from 'vuex'
    export default {
    //import引入的组件需要注入到对象中才能使用
    components: {},
    data() {
    //这里存放数据
    return {
        del_show:false
    };
    },
    //监听属性 类似于data概念
    computed: {
        ...mapGetters(['carList','allMoney','allSelsect'])
    },
    //监控data中的数据变化
    watch: {},
    //方法集合
    methods: {
        ...mapActions(['headTitle','addNum','jianNum','select','selectAll','del','showBtm'])
    },
    //生命周期 - 创建完成（可以访问当前this实例）
    created() {
        this.headTitle('购物车')
        this.showBtm(true)
    },
    //生命周期 - 挂载完成（可以访问DOM元素）
    mounted() {

    },
    beforeCreate() {}, //生命周期 - 创建之前
    beforeMount() {}, //生命周期 - 挂载之前
    beforeUpdate() {}, //生命周期 - 更新之前
    updated() {}, //生命周期 - 更新之后
    beforeDestroy() {}, //生命周期 - 销毁之前
    destroyed() {}, //生命周期 - 销毁完成
    activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
    }
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
    .car{
        padding-top:90px;
        padding-bottom:200px;
    }
    .edit{
        width:100%;
        display:flex;
        height:90px;
        align-items:center;
        justify-content: flex-end;
    }
    .edit-text{
        width:164px;
        height:52px;
        border-radius:50px;
        text-align:center;
        line-height:52px;
        font-size:14px;
        color:rgb(102,102,102);
        border:2px solid rgb(102,102,102);
        margin-right:30px;
    }
    .edit-texts{
        color:#f00;
        border:2px solid #f00;
    }
    .car-list{
        width:94%;
        padding:30px 20px;
        background:#fff;
        margin:0 auto;
        margin-bottom:30px;
        border-radius:10px;
        display:flex;
        align-items:center;
    }
    .choose-icon{
        width:42px;
        height:42px;
        display:block;
    }
    .car-img{
        width:190px;
        height:190px;
        display:block;
        border-radius:6px;
        margin-right:16px;
        margin-left:10px;
    }
    .car-fr{
        width:400px;
    }
    .car-name{
        font-size:28px;
        color:#000;
        text-align:left;
        margin-bottom:30px;
    }
    .fr-btm{
        width:100%;
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
    }
    .money-old{
        font-size:11px;
        color:rgb(102,102,102);
        text-decoration: line-through;
    }
    .money-now{
        font-size:28px;
        color:#f00;
    }
    .car-num{
        display:flex;
        align-items:center;
    }
    .num-icon{
        display:block;
        width:40px;
        height:40px;
    }
    .car-nums{
        display:block;
        width:60px;
        height:40px;
        text-align:center;
        line-height:40px;
        font-size:12px;
        color:rgb(41,41,41);
    }
    .car-btm{
        width:100%;
        height:100px;
        display:flex;
        align-items:center;
        background:#fff;
        position: fixed;
        left:0;
        bottom:100px;
        padding-left:30px;
        justify-content: space-between;
    }
    .btm-fl{
        display: flex;
        align-items:center;
    }
    .all-text{
        margin-left:20px;
        font-size:26px;
        color:#777;
    }
    .all-money{
        font-size:32px;
        color:#000;
        margin-left:20px;
    }
    .pay{
        width:220px;
        text-align:center;
        line-height:100px;
        font-size:36px;
        color:#fff;
        font-weight:bold;
        background:#f00;

    }
    .del{
        width:164px;
        height:52px;
        border-radius:50px;
        text-align:center;
        line-height:52px;
        font-size:14px;
        color:#f00;
        border:2px solid #f00;
        margin-right:30px;
    }
</style>


